.incomesModal {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(215, 14, 14, 0.75);
  display: none;
  justify-content: center;
  align-items: center;
}

.modal-is-open {
  display: flex;
}

.modal-container {
  position: relative;
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
}
